.container{
    height: 25px;
    display: flex;
    gap: 10px;
    justify-content: center;
    align-items: center;
}

.container button{
    width: 80px ;
    height: 25px;
}

.container p{
	color: #ffffff;
	text-shadow: 1px 1px rgba(0, 0, 0, 0.914);
	
}

.container input{
		background-color: #34495e;
		border-radius: 10px 0 0 10px;
		border: none;
		width: 30px;
		height: 20px;
		padding-left: 8px;
		color: white;
		font-size: 12px;
		font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
	  
}
.black{
	font-size: 11px;
	color: #fff;
	text-shadow: 0 1px 1px rgba(0,0,0,.5);
	-webkit-border-radius: 5px;
	-moz-border-radius: 5px;
	border-radius: 5px;
	background-color: #eee;
	-webkit-box-shadow: inset 0 1px rgba(255,255,255,.4);
	-moz-box-shadow: inset 0 1px rgba(255,255,255,.4);
	box-shadow: inset 0 1px rgba(255,255,255,.4);
	border: solid 1px #34495e;
	background: -webkit-linear-gradient(bottom, #34495e, #4d4d4d);
	background: -moz-linear-gradient(bottom, #34495e, #4d4d4d);
	background: -o-linear-gradient(bottom, #34495e, #4d4d4d);
	background: -ms-linear-gradient(bottom, #34495e, #4d4d4d);
	background: linear-gradient(to top, #34495e, #4d4d4d);
	display: inline-block;
	margin-top: 20px;
	margin-bottom: 20px;
}
.black:hover{
	background: -webkit-linear-gradient(bottom, #34495e, #131313);
	background: -moz-linear-gradient(bottom, #34495e, #131313);
	background: -o-linear-gradient(bottom, #34495e, #131313);
	background: -ms-linear-gradient(bottom, #34495e, #131313);
	background: linear-gradient(to top, #34495e, #131313);
}